.removefavorite-button {
  width: 168px;
  height: 28px;
  text-align: center;
  color: #111321;
  cursor: pointer;
  background-color: #fff;
  border-color: #0000;
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  font-family: Manrope;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: flex;
  position: absolute;
  bottom: 15px;
  right: 15px;
}

.removefavorite-button:after {
  content: "";
  width: 16px;
  height: 16px;
  background-image: url("filledheart.64d1db18.svg");
  background-repeat: no-repeat;
  margin-left: 4px;
}

